import React from 'react'
import "./ItemLista.css"

const ItemLista = props => {
  return (
    <div className="card mb-3 cardFineTune">
      <div className="row no-gutters">
        <div className="col-4">
          <img src={props.foto} className="card-img photoFit" alt="Foto" />
        </div>
        <div className="col-8">
          <div className="card-body">
            <h5 className="card-title">Estandista: {props.nome}</h5>
            <p className="card-text">Negócio: {(props.negocio)}</p>
            <p className="card-text"><small className="text-muted">Estande: {(props.estande)}</small></p>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ItemLista