import React, { Component } from 'react'

import Conecta from './Conecta'
import ItemLista from './ItemLista'

import './Listagem.css'

export default class Listagem extends Component {

  state = {
    estandistas: []
  }

  async componentDidMount() {
    // verifica se há conexão com a internet
    if (navigator.onLine) {      
      const lista = await Conecta.get('453959cc-9b2e-47de-afd8-4a6095ed5e16')
      this.setState({estandistas: lista.data})
      
      // salva em localStorage os dados para posterior acesso (quando 
      // estiver sem conexão)
      localStorage.setItem('estandistas-local', JSON.stringify(lista.data))
    } else {
      // se não tiver conexão, obtém a lista anteriormente salva em localStorage
      const lista = await JSON.parse(localStorage.getItem('estandistas-local'))
      this.setState({estandistas: lista})
    }
  }

  render() {
    return (
      <div className="container mt-2">
        <div className="row">
          {this.state.estandistas.map(estandista => (
            <ItemLista key={estandista.nome}
               nome={estandista.nome}
               negocio={estandista.negocio}
               estande={estandista.estande}
               foto={estandista.foto} />
          ))}
        </div>        
      </div>
    )
  }
}
